@-webkit-keyframes logoFadeUp {
    0% {
        transform: translateY(0)
    }
    50% {
        transform: translateY(calc(-100% - 25px));
        opacity: 1
    }
    51% {
        transform: translateY(calc(-100% - 25px));
        opacity: 0
    }
    52% {
        transform: translateY(calc(100% + 25px))
    }
    86% {
        opacity: 1
    }
    100% {
        transform: translateY(0)
    }
}
@keyframes logoFadeUp {
    0% {
        transform: translateY(0)
    }
    50% {
        transform: translateY(calc(-100% - 25px));
        opacity: 1
    }
    51% {
        transform: translateY(calc(-100% - 25px));
        opacity: 0
    }
    52% {
        transform: translateY(calc(100% + 25px))
    }
    86% {
        opacity: 1
    }
    100% {
        transform: translateY(0)
    }
}

